import React, {Component} from 'react';
import styles from "./Show.module.scss"

class Show extends Component {
    render() {
        return (
            <div id={styles.show}>
                <div className={styles.miniPorgram + " animated fadeInDown delay-1s"}>
                    <div className={styles.image}>
                        <img src={require("../../../images/home/show/1.gif")} alt=""/>
                        <img src={require("../../../images/home/show/2.gif")} alt=""/>
                    </div>
                    <h2>自制查询星座和亲戚关系的小程序</h2>
                </div>
                <div className={styles.game + " animated fadeInDown delay-2s"}>
                    <img src={require("../../../images/home/show/3.gif")} alt=""/>
                    <h2>使用unity仿制保卫萝卜小游戏</h2>
                </div>
                <div className={styles.htmlGame + " animated fadeInDown"}>
                    <img src={require("../../../images/home/show/18125419-a9d252c0c1284ea2.gif")} alt=""/>
                    <h2>最开始学习前端的时候用HTML和jQuery制作的一个跳跃鸟小游戏</h2>
                </div>
            </div>
        );
    }
}

export default Show;