import React, {Component} from 'react';
import styles from "./Home.module.scss";
import Hello from "./Hello/Hello";
import Navigation from "./Navigation/Navigation";
import Introduction from "./Introduction/Introduction";
import Show from "./Show/Show";
import image1 from "../../images/home/coronavirus-4937226_1920.jpg";
import image2 from "../../images/home/workplace-2303851_1920.jpg";
import image3 from "../../images/home/wall-3372631_1920.jpg";

class Header extends Component {
    constructor(props) {
        super(props);
        this.state = {
            count: 1
        };
        this.bgImage = React.createRef();
    }

    render() {
        const {count} = this.state;
        return (
            <div id={styles.home}>
                {/*背景图*/}
                <div ref={this.bgImage} className={styles.backGround}/>
                {/*背景遮罩*/}
                <div className={styles.mask}></div>
                {/*导航*/}
                <Navigation
                    _changePage={(page) => this._changePage(page)}
                    count={count}
                />
                {/*欢迎页*/}
                {count === 1 ? <Hello/> : ""}
                {/*自我介绍页*/}
                {count === 2 ? <Introduction/> : ""}
                {/*展示页*/}
                {count === 3 ? <Show/> : ""}
            </div>
        );
    }


    async _changePage(page) {
        await this.setState({
            count: page
        });

        let bgImage = this.bgImage.current;
        switch (page) {
            case 1 :
                bgImage.style.backgroundImage = `url(${image1})`;
                break;
            case 2 :
                bgImage.style.backgroundImage = `url(${image2})`;
                break;
            case 3:
                bgImage.style.backgroundImage = `url(${image3})`;
                break;
            default:
                break;
        }

    }

    componentDidMount() {
        this._touchChangePage();
    }


    //移动端手指滑动翻页
    _touchChangePage() {
        let startx, starty;

        //获得角度
        function getAngle(angx, angy) {
            return Math.atan2(angy, angx) * 180 / Math.PI;
        };

        //根据起点终点返回方向 1向上 2向下 3向左 4向右 0未滑动
        function getDirection(startx, starty, endx, endy) {
            let angx = endx - startx;
            let angy = endy - starty;
            let result = 0;

            //如果滑动距离太短
            if (Math.abs(angx) < 2 && Math.abs(angy) < 2) {
                return result;
            }

            let angle = getAngle(angx, angy);
            if (angle >= -135 && angle <= -45) {
                result = 1;
            } else if (angle > 45 && angle < 135) {
                result = 2;
            } else if ((angle >= 135 && angle <= 180) || (angle >= -180 && angle < -135)) {
                result = 3;
            } else if (angle >= -45 && angle <= 45) {
                result = 4;
            }

            return result;
        }

        //手指接触屏幕
        document.addEventListener("touchstart", (e) => {
            startx = e.touches[0].pageX;
            starty = e.touches[0].pageY;
        }, false);
        //手指离开屏幕
        document.addEventListener("touchend", (e) => {
            let endx, endy;
            endx = e.changedTouches[0].pageX;
            endy = e.changedTouches[0].pageY;
            let direction = getDirection(startx, starty, endx, endy);
            let count = this.state.count;
            switch (direction) {
                case 0:
                    break;
                case 1:
                    if (count < 3) {
                        count += 1;
                    }
                    this._changePage(count);
                    break;
                case 2:
                    if (count > 1) {
                        count -= 1;
                    }
                    this._changePage(count);
                    break;
                case 3:
                    break;
                case 4:
                    break;
                default:
            }
        }, false);
    }
}

export default Header;