import React, {Component} from 'react';
import styles from "./Hello.module.scss";

class Hello extends Component {
    render() {
        return (
            <div id={styles.Hello}>
                <div className={styles.body}>
                    {/*主体*/}
                    <div className={styles.text}>
                        <h1 className="animated bounceInDown">2020年即使在困难</h1>
                        <h2 className="animated bounceInDown delay-1s">也不要停止向前</h2>
                    </div>
                    {/*跳转链接*/}
                    <div className={styles.button + " animated rollIn delay-2s"}>
                        <span className={"iconfont icon-shouzhi animated shake slow infinite " + styles.finger}></span>
                        <a href="https://mikumirai.github.io/mylog/index">
                            <button className="myButton">我的个人主页</button>
                        </a>
                    </div>
                </div>
            </div>
        );
    }
}

export default Hello;