import React, {Component} from 'react';
import styles from "./Navigation.module.scss";

class Navigation extends Component {
    render() {
        const {_changePage, count} = this.props;
        return (
            <div id={styles.navigation}>
                <ul className={styles.list}>
                    <li onClick={() => _changePage(1)}><span className="iconfont icon-iconset0111
 "/><span
                        className={styles.text + " " + (count === 1 ? styles.choose : "")}> 首页</span></li>
                    <li onClick={() => _changePage(2)}><span className="iconfont icon-wo"/><span
                        className={styles.text + " " + (count === 2 ? styles.choose : "")}> 个人介绍</span></li>
                    <li onClick={() => _changePage(3)}><span className="iconfont icon-jieshao"/><span
                        className={styles.text + " " + (count === 3 ? styles.choose : "")}> 展示</span></li>
                </ul>
                <ul className={styles.button}>
                    <li></li>
                </ul>
            </div>
        );
    }
}

export default Navigation;